@import url(https://fonts.googleapis.com/css?family=Roboto:400,300,500);

*,
*:after,
*:before {
    box-sizing: border-box;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
    border: 1px solid #e6b333;
    -webkit-text-fill-color: #e6b333;
    -webkit-box-shadow: 0 0 0px 1000px #22272d inset;
    transition: background-color 5000s ease-in-out 0s;
}

html,
body {
    background: #1a1f25;
    font-family: 'Roboto', sans-serif;
}

.clear {
    clear: both;
}

#manPeopleUser,
#lockLocker {
    fill: white;
}

.wrapper {
    width: 300px;
    margin: 12% auto;
}

h1 {
    color: color-mix(in srgb-linear, white, rgb(9, 241, 60));
}

button:hover,
input[type='submit']:hover {
    background: rgb(11, 182, 48);
    border: 1px solid #e6b333;
}

form {
    margin-top: 0;
    position: relative;
}

.clear {
    clear: both;
}

.sign-in {
    float: left;
    color: white;
    font-size: 1.3em;
}

button {

    color: #7f8084;
    border: 1px solid #22272d;
    padding: 7px 15px;
    border-radius: 3px;
    font-size: 0.8em;
    cursor: pointer;
}

button:hover {
    color: #d3d3d3;
}

.lock {
    position: absolute;
    width: 30px;
    height: 30px;
    margin-top: 10px;
    padding: 7px;
    left: 5px;
}

.lock::after {
    content: '';
    width: 1px;
    height: 30px;
    position: absolute;
    background: #22272d;
    top: 0px;
    left: 100%;
}

.user {
    position: absolute;
    width: 30px;
    height: 30px;
    margin-top: 10px;
    padding: 7px;
    left: 5px;
}

.user::after {
    content: '';
    width: 1px;
    height: 30px;
    position: absolute;
    background: #22272d;
    top: 0px;
    left: 100%;
}

input {
    width: 100%;
    padding: 5px;
    height: 40px;
    border-radius: 3px;
    margin: 5px 0;
    outline: none;
}

input[type='text']:focus,
input[type='password']:focus {
    border: 1px solid white;
    box-shadow: none;
}

.userInput:focus .user {
    background: white !important;
}

input[type='text'] {
    background: transparent;
    border: 2px solid #22272d;
    padding-left: 45px;
    color: #e6b333;
}

input[type='password'] {
    background: transparent;
    border: 2px solid #22272d;
    padding-left: 45px;
    color: #e6b333;
}

input[type='submit'] {
    background: #e6b333;
    border: none;
    color: white;
    text-align: center;
    font-size: 0.8em;
    cursor: pointer;
}









.checkLabel {
    font-size: 0.8em;
    color: #7f8084;
    margin-top: 10px;
    float: left;
    padding: 3px 0;
    margin-left: 10px;
}

.forgotLabel {
    font-size: 0.8em;
    color: #7f8084;
    margin-top: 10px;
    float: right;
    padding: 3px 0;
    cursor: pointer;
}