#authFormlogPas {
    margin-top: 0;

    input,
    button {
        width: 100%;
        border-radius: 3px;
        border: none;
    }

    input[type='submit'] {
        background: #e6b333;
        border: none;
        color: white;
        text-align: center;
        font-size: 0.8em;
        cursor: pointer;
    }

    input[type='submit']:hover {
        background: rgb(11, 182, 48);
        border: 1px solid #e6b333;
    }


}

.wrapper {
    width: 300px;
    margin: 12% auto;
}

form {
    position: relative;
}

.clear {
    clear: both;
}

.signIn {
    float: left;
    color: white;
    font-size: 1.3em;
}

.button {
    float: right;
    color: white;
    border: 1px solid #22272d;
    padding: 7px 15px;
    border-radius: 3px;
    font-size: 0.8em;
    cursor: pointer;
}


button:hover,
input[type='submit']:hover {
    background: rgb(11, 182, 48);
    border: 1px solid #e6b333;
}

.lock {
    position: absolute;
    width: 30px;
    height: 30px;
    margin-top: 10px;
    padding: 7px;
    left: 5px;
}

.lock::after {
    content: '';
    width: 1px;
    height: 30px;
    position: absolute;
    background: #22272d;
    top: 0px;
    left: 100%;
}

.user {
    position: absolute;
    width: 30px;
    height: 30px;
    margin-top: 10px;
    padding: 7px;
    left: 5px;
}

.user::after {
    content: '';
    width: 1px;
    height: 30px;
    position: absolute;
    background: #22272d;
    top: 0px;
    left: 100%;
}

input {
    width: 100%;
    padding: 5px;
    height: 40px;
    border-radius: 3px;
    margin: 5px 0;
    outline: none;
}

input[type='login']:focus,
input[type='password']:focus {
    border: 1px solid white;
    box-shadow: none;
}

.userInput:focus .user {
    background: white !important;
}

input[type='login'] {
    background: transparent;
    border: 2px solid #22272d;
    padding-left: 45px;
    color: #e6b333;
}

input[type='password'] {
    background: transparent;
    border: 2px solid #22272d;
    padding-left: 45px;
    color: #e6b333;
}

input[type='submit'] {
    background: #e6b333;
    border: none;
    color: white;
    text-align: center;
    font-size: 0.8em;
    cursor: pointer;
}

.radioCheck {
    width: 50px;
    height: 20px;
    border: 1px solid #22272d;
    border-radius: 30px;
    margin-top: 10px;
    float: left;
    text-align: center;
    padding: 4px 0;
    color: #e6b333;
    font-size: 0.65em;
    position: relative;
}

.forgotLabel {
    font-size: 0.8em;
    color: #7f8084;
    margin-top: 10px;
    float: right;
    padding: 3px 0;
    cursor: pointer;
}