* {
    border-radius: 5px;
    color: black;
}

textarea {
    height: 100px;
    width: 100%;
}

.kartaDrogowa {
    display: grid;
    align-items: center;
    justify-items: stretch;
    align-content: stretch;
    justify-content: center;


}



.formStartWay {
    margin-top: 5px;
    text-align: justify;



}

.formStartWay>label,
.formStartWay>input {
    display: block;
    width: 100%;
    height: 4rch;
}

.dropdown {
    text-align: center;
    width: 100%;
    cursor: pointer;
    margin-top: 5px;
    background-color: color-mix(in srgb-linear, white, rgb(229, 236, 245));
    height: 4rch;
}

.formStartWay>.btnStartWay,
.formStartWay>.btnFinishWay {
    text-align: center;
    width: 100%;
    cursor: pointer;
    margin-top: 5px;
    height: 4rch;
}

.btnFinishWay {
    pointer-events: none;
    background-color: color-mix(in srgb-linear, white, rgb(227, 51, 18));
}

.btnStartWay {
    background-color: color-mix(in srgb-linear, white, rgb(9, 241, 60));
}

#kmStart {
    background-color: color-mix(in srgb-linear, white, rgb(229, 236, 245));
}

#kmFinish {
    background-color: color-mix(in srgb-linear, white, rgb(229, 236, 245));
}

.btnSendToDb {
    position: absolute;
    left: 0%;
    transform: translate(112%);
}

b {
    color: white;
}

span {
    color: aqua;
    height: 4rch;
    margin-top: 5px;
    display: block;
}

.devLogo {
    position: absolute;
    bottom: 0;
    right: 22px;
    color: aqua;
    font-size: 5px;
    margin-top: 5px;
    display: block;
}

img {
    width: 102px;
    height: 102px;
    position: fixed;
    z-index: 10;
}