@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;500&display=swap');

html,
body {
    background: #1a1f25;
    color: white;
    font-family: 'Roboto', sans-serif;
    margin: 0;
    padding: 0;
    height: 100%;
}

.container {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

.header {
    background: #22272d;
    padding: 10px;
    text-align: center;
}

.header h1 {
    margin: 0;
}

.nav {
    display: flex;
    justify-content: center;
    background: #333;
    padding: 10px 0;
}

a {
    text-decoration: none;
    /* Убираем подчёркивание */
    color: inherit;
    /* Цвет ссылки будет таким же, как у окружающего текста */
    padding: 0;
    /* Сбрасываем отступы */
    margin: 0;
    /* Сбрасываем внешние отступы */
    cursor: default;
    /* Сбрасываем курсор */
}

a:hover,
a:focus,
a:active {
    text-decoration: none;
    color: inherit;
}

.nav button:hover {
    background: color-mix(in srgb-linear, white, rgb(9, 241, 60));
    ;
    border: 1px solid #e6b333;
}

.content {
    flex: 1;
    padding: 20px;
    text-align: center;
}

.footer {
    background: #22272d;
    padding-top: 15px;
    padding-left: 80%;
    text-align: center;
}



@media (max-width: 768px) {
    .nav {
        flex-direction: column;
    }

    .nav a {
        margin: 5px 0;
    }
}

.linkBtnMain {
    padding: 15px;
    height: 42px;
    margin: 2px;
    background: #e6b333;
    border: none;
    color: white;
    text-align: center;
    font-size: 0.8em;
    cursor: pointer;
}