html {
  text-align: center;
  font-size: 16px;
  height: 97%;
}

.contRoadmap>h1 {
  color: black
}

.btnsYeas {
  margin-top: 40px;
}

.btnsRoadmaps {
  margin: 5px 5px 5px 5px;
  width: 15%;
}

.contRoadmap {
  height: 97%;
  display: grid;
  justify-items: center;
  align-items: center;
  justify-content: center;
  align-content: center;
  margin-top: 5px;
  position: absolute;
  transform: translate(50%);
  right: 50%;
  width: 10%;
  height: 10%;
  background-color: #b3e8bb;

}

#customers {
  margin-top: 15px;

  border-collapse: collapse;
  width: 90%;

}

#customers td,
#customers th {
  border: 2px solid black;
  padding: 8px;
}

#customers tr {
  background-color: white;
}

#customers tr:nth-child(even) {
  background-color: #ff2;
}

#customers tr:hover {
  background-color: orange;
}

#customers th {

  padding-top: 12px;
  padding-bottom: 12px;
  text-align: left;
  background-color: gray;
  color: white;
}

.btnSGroupDelBack {
  button {
    margin: 10px;
  }

  margin-top: 50px;
  width: 80%;
}

.linkBtnMain {
  padding: 15px;
  height: 42px;
  margin: 2px;
  background: #e6b333;
  border: none;
  color: white;
  text-align: center;
  font-size: 0.8em;
  cursor: pointer;
}

.linkBtnMain:hover {
  background: color-mix(in srgb-linear, white, rgb(9, 241, 60));
  border: 2px solid;
}