@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;500&display=swap');

html,
body {
    text-align: center;
    background: #1a1f25;
    color: white;
    font-family: 'Roboto', sans-serif;
    margin: 0;
    padding: 0;
    height: 100%;
}

.linkBtnMain {
    display: block;
    padding: 15px;
    height: 42px;
    margin: 2px;
    background: #e6b333;
    border: none;
    color: white;
    text-align: center;
    width: 90%;
    font-size: 0.8em;
    cursor: pointer;
}

.container {

    display: flex;

    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;
    justify-content: center;


}

.formCartCount {

    display: grid;
    justify-items: center;
    align-items: center;
    justify-content: center;
    align-content: center;


    label {
        color: white
    }

    form {
        margin-top: 50px;
    }

    /* Убираем внутренние и внешние стрелки для input[type="number"] в WebKit браузерах */
    input[type=number]::-webkit-inner-spin-button,
    input[type=number]::-webkit-outer-spin-button {
        -webkit-appearance: none;
        margin: 0;

    }

    input[type="number"] {
        height: 41px;
        padding-top: 2px;
        text-align: center;
    }








    input,
    button {
        width: 30%;
        border-radius: 3px;
        border: none;

    }


    button {
        float: none;
        height: 42px;
        margin: 2px;
        background: #e6b333;
        border: none;
        color: white;
        text-align: center;
        font-size: 0.8em;
        cursor: pointer;
    }

    .nav {
        display: flex;
        justify-content: center;
        background: #333;
        padding: 10px 0;
    }







    #submitCartCounts,
    #BtnClearLocalStorage {
        margin-top: 15px;
        background: #e6b333;
        border: none;
        color: white;
        text-align: center;
        font-size: 0.8em;
        cursor: pointer;
        width: 90%;
        border-radius: 3px;
    }


    button:hover,
    #submitCartCounts:hover,
    #BtnClearLocalStorage:hover {
        background: color-mix(in srgb-linear, white, rgb(9, 241, 60));
        ;
        border: 1px solid #e6b333;
    }

    .content {
        flex: 1;
        padding: 20px;
        text-align: center;
    }

    .footer {
        background: #22272d;
        padding: 10px;
        text-align: center;
    }



    @media (max-width: 768px) {
        .nav {
            flex-direction: column;
        }

        .nav a {
            margin: 5px 0;
        }
    }
}